.Dice {
    cursor: pointer;     
    background-color: #182b3e;  
    width: 55px;  
    height: 55px;
    border-radius: 10%;
    box-shadow: inset 0 2px hsl(249, 27%, 71%), inset 0 -2px hsl(249, 27%, 71%), inset 2px 0 #d7d7d7, inset -2px 0 #d7d7d7, rgba(0, 0, 0, 0.35) 0px 5px 15px;
    position: relative;
}

.Dice--locked {
    opacity: .4;
    box-shadow: none;
    box-shadow: none;
}

.rotate {
    animation: rotate .5s ease-out;
}

.Dot {
    width: 8px;
    height: 8px;
    border-radius: 100px;
    background-color:hsl(249, 27%, 71%);
    position: absolute;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.top {
    top: 5px;
}

.middle {
    top: 50%;
    transform: translateY(-50%);
}

.bottom {
    bottom: 5px;
}

.left {
    left: 5px;
}

.center {
    left: 50%;
    transform: translateX(-50%);
}

.right {
    right: 5px;
}

.center-center {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}

@media screen and (max-height: 630px) {
    .Dice {
        width: 40px;
        height: 40px;
    }
}