.Rule {
    cursor: pointer;
    border-bottom: 1px solid hsl(208, 100%, 10%);
    margin-bottom: 4px;
    padding-bottom: 4px;
    font-size: large;
    padding-top: 1px;
    color: hsl(238, 35%, 19%);
    font-weight: 600;
    display: flex;
    justify-content: space-between;   
    font-family: "Segoe Print";
}

.Rule--disabled {
    cursor: not-allowed;
    opacity: .5;
}

.Rule--crossed {
    text-decoration: line-through;

    opacity: .5;
}